<template>
    <div class="markdown">
        <mavonEditor ref=md :toolbars="markdownOption" @imgAdd="imgAdd" @change="change" v-model="html"></mavonEditor>
    </div>
</template>
<script type="text/javascript">
    import { mavonEditor } from 'mavon-editor'
    import axios from 'axios';
    export default {
        components:{
            mavonEditor
        },
        props:['value'],
        data(){
            return {
                markdownOption: {
                    bold: true, // 
                    italic: true, // 
                    header: true, // 
                    underline: true, // 
                    strikethrough: true, // 
                    mark: true, // 
                    superscript: true, // 
                    subscript: true, // 
                    quote: true, // 
                    ol: true, // 
                    ul: true, // 
                    link: true, // 
                    imagelink: true, // 
                    code: true, // code
                    table: true, // 
                    fullscreen: true, // 
                    readmodel: true, // 
                    htmlcode: true, // html
                    help: true, // 
                    /* 1.3.5 */
                    undo: true, // 
                    redo: true, // 
                    trash: true, // 
                    save: true, // eventssave
                    /* 1.4.2 */
                    navigation: true, // 
                    /* 2.1.8 */
                    alignleft: true, // 
                    aligncenter: true, // 
                    alignright: true, // 
                    /* 2.2.1 */
                    subfield: true, // 
                    preview: true, // 
                },
                html:''
            }
        },
        created(){

        },
        watch:{
            value: function () {
                this.html = this.value;
            }
        },
        methods:{
            onSubmit(){
                console.log(this.html);
                console.log(this.$refs.md.d_render);
            },
            imgAdd(pos, $file){
                // ..
                var formdata = new FormData();
                formdata.append('file', $file);
                axios({
                    url: '/upload/img',
                    method: 'post',
                    data: formdata,
                    headers: { 'Content-Type': 'multipart/form-data', 'type': 'blog' },
                }).then((url) => {
                    // .url![...](0) -> ![...](url)
                    // $vm.$img2Url 
                    // $vm.$img2Url(pos, url);
                    url = '/uploads/' + url.data.data;
                    console.log(pos,'pospospospospospospospos');
                    this.$refs.md.$img2Url(pos, url)
                })
            },
            change(value, render){
                this.$emit('update',{ value, render })
            }
        },
    }
</script>
<style lang="scss">
    @import "~mavon-editor/dist/css/index.css";
    .markdown{width: 99%;}
</style>
